import { useAppDispatch, useAppSelector } from "../../redux/hooks";
import "./editor.sass";
import { markdownSelector, updateMarkdown } from "./editorSlice";

const Editor = () => {
  const dispatch = useAppDispatch();
  const markdown = useAppSelector(markdownSelector);

  return (
    <div id="editor-wrapper">
      <h2>Editor</h2>
      <textarea
        name="textarea"
        id="editor"
        value={markdown}
        onChange={(e) => dispatch(updateMarkdown(e.target.value))}
      ></textarea>
    </div>
  );
};

export default Editor;
