import { ReactMarkdown } from "react-markdown/lib/react-markdown";
import remarkBreaks from "remark-breaks";
import { useAppSelector } from "../../redux/hooks";
import { markdownSelector } from "../editor/editorSlice";
import "./previewer.sass";

// import Showdown from "showdown";
// const markdownConverter = new Showdown.Converter({ simpleLineBreaks: true, ellipsis: false });

// import { marked } from "marked";
// marked.setOptions({ breaks: true });

const Previewer = () => {
  const markdown = useAppSelector(markdownSelector);

  return (
    <div className="previewer-wrapper">
      <h2>Previewer</h2>
      <div id="preview">
        <ReactMarkdown
          children={markdown}
          remarkPlugins={[remarkBreaks]}
          linkTarget="_blank"
        ></ReactMarkdown>
      </div>
      {/* <div
        id="preview"
        dangerouslySetInnerHTML={{
          __html: markdownConverter.makeHtml(markdown),
        }}
      ></div> */}
      {/* <div
        id="preview"
        dangerouslySetInnerHTML={{
          __html: marked.parse(markdown),
        }}
      ></div> */}
    </div>
  );
};

export default Previewer;
